
.aboutuscontainer{
    top: 30px;
    position: relative;
    width: 100%;
    max-width: 600px;
    margin: 20px auto;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    overflow-wrap: break-word;
    padding: 20px;
    border: 1px solid rgba(119, 119, 119, 0.596);
    border-radius: 8px;
    align-items: center;
    display:inline-block;
    vertical-align: middle;
}

.aboutusspacing{
    margin-top: 3%;
    margin-bottom: 3%;
}

.headingaboutus{
    padding-top: 7%;
    text-align: center;

}

.mgaboutheight{
    margin-bottom: 25em;
}


.rrow {
display: flex;
}








.imageaboutus{
    max-height: 50px;
    max-width: 50px;
}

.bbbox{
    width: 500px;
    position: relative;
    display: grid;
    background: white;
    padding: 20px;
    border: 1px solid rgba(119, 119, 119, 0.596);
    border-radius: 8px;
}


.iimage-box img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    }